export function checkResponse(res:Response) {
  if (res.ok) {
    return res.json();
  }
  return Promise.reject(` ${res.status}`);
}

//      
function dropHMS(date: Date) {
  date.setHours(0);
  date.setMinutes(0);
  date.setSeconds(0, 0);
}

export function orderDate(orderDate: Date) {
  let today = new Date(),
    yesterday = new Date(),
    roomLastMessageDate = new Date(orderDate);

  yesterday.setDate(today.getDate() - 1);

  dropHMS(today);
  dropHMS(yesterday);
  dropHMS(roomLastMessageDate);

  const months = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  const dateMinutes = ("0" + orderDate.getMinutes()).slice(-2);
  const timeZone =
    orderDate.getTimezoneOffset() / 60 < 0
      ? `i-GMT+${-orderDate.getTimezoneOffset() / 60}`
      : `i-GMT-${-orderDate.getTimezoneOffset() / 60}`;
  const dateMonth = months[orderDate.getMonth()];

  if (orderDate) {
    if (today.getTime() === roomLastMessageDate.getTime()) {
      return `, ${orderDate.getHours()}:${dateMinutes} ${timeZone}`;
    } else if (yesterday.getTime() === roomLastMessageDate.getTime()) {
      return `, ${orderDate.getHours()}:${dateMinutes} ${timeZone}`;
    } else {
      return `${orderDate.getDate()} ${dateMonth} ${orderDate.getFullYear()}, ${orderDate.getHours()}:${dateMinutes} ${timeZone}`;
    }
  }
}
